#glitch {
    background: #272643;
}
#glitch h2{
    color: #FFFFFF;
}
.glitch {
    color: white;
    font-size: 100px;
    position: relative;
    width: 400px;
    margin: 0 auto;
}

@keyframes noise-anim {
    0% {
        clip: rect(26px, 9999px, 1px, 0);
    }

    5% {
        clip: rect(13px, 9999px, 85px, 0);
    }

    10% {
        clip: rect(17px, 9999px, 44px, 0);
    }

    15% {
        clip: rect(42px, 9999px, 44px, 0);
    }

    20% {
        clip: rect(6px, 9999px, 68px, 0);
    }

    25% {
        clip: rect(40px, 9999px, 16px, 0);
    }

    30% {
        clip: rect(14px, 9999px, 73px, 0);
    }

    35% {
        clip: rect(92px, 9999px, 10px, 0);
    }

    40% {
        clip: rect(16px, 9999px, 43px, 0);
    }

    45% {
        clip: rect(54px, 9999px, 50px, 0);
    }

    50% {
        clip: rect(98px, 9999px, 96px, 0);
    }

    55% {
        clip: rect(52px, 9999px, 77px, 0);
    }

    60% {
        clip: rect(74px, 9999px, 73px, 0);
    }

    65% {
        clip: rect(97px, 9999px, 17px, 0);
    }

    70% {
        clip: rect(6px, 9999px, 64px, 0);
    }

    75% {
        clip: rect(51px, 9999px, 87px, 0);
    }

    80% {
        clip: rect(66px, 9999px, 14px, 0);
    }

    85% {
        clip: rect(21px, 9999px, 63px, 0);
    }

    90% {
        clip: rect(38px, 9999px, 72px, 0);
    }

    95% {
        clip: rect(63px, 9999px, 33px, 0);
    }

    100% {
        clip: rect(66px, 9999px, 70px, 0);
    }
}

.glitch:after {
    content: attr(data-text);
    position: absolute;
    left: 2px;
    text-shadow: -1px 0 red;
    top: 0;
    color: white;
    background: black;
    overflow: hidden;
    clip: rect(0, 900px, 0, 0);
    animation: noise-anim 2s infinite linear alternate-reverse;
}

@keyframes noise-anim-2 {
    0% {
        clip: rect(78px, 9999px, 16px, 0);
    }

    5% {
        clip: rect(88px, 9999px, 98px, 0);
    }

    10% {
        clip: rect(10px, 9999px, 19px, 0);
    }

    15% {
        clip: rect(81px, 9999px, 46px, 0);
    }

    20% {
        clip: rect(82px, 9999px, 2px, 0);
    }

    25% {
        clip: rect(93px, 9999px, 8px, 0);
    }

    30% {
        clip: rect(22px, 9999px, 71px, 0);
    }

    35% {
        clip: rect(84px, 9999px, 40px, 0);
    }

    40% {
        clip: rect(1px, 9999px, 72px, 0);
    }

    45% {
        clip: rect(46px, 9999px, 51px, 0);
    }

    50% {
        clip: rect(74px, 9999px, 43px, 0);
    }

    55% {
        clip: rect(86px, 9999px, 12px, 0);
    }

    60% {
        clip: rect(52px, 9999px, 55px, 0);
    }

    65% {
        clip: rect(87px, 9999px, 29px, 0);
    }

    70% {
        clip: rect(13px, 9999px, 93px, 0);
    }

    75% {
        clip: rect(65px, 9999px, 53px, 0);
    }

    80% {
        clip: rect(52px, 9999px, 89px, 0);
    }

    85% {
        clip: rect(33px, 9999px, 100px, 0);
    }

    90% {
        clip: rect(39px, 9999px, 44px, 0);
    }

    95% {
        clip: rect(38px, 9999px, 36px, 0);
    }

    100% {
        clip: rect(95px, 9999px, 29px, 0);
    }
}

.glitch:before {
    content: attr(data-text);
    position: absolute;
    left: -2px;
    text-shadow: 1px 0 blue;
    top: 0;
    color: white;
    background: black;
    overflow: hidden;
    clip: rect(0, 900px, 0, 0);
    animation: noise-anim-2 3s infinite linear alternate-reverse;
}