@-webkit-keyframes mask-move {
    0% {
        transform: translateX(0);
    }

    100% {
        transform: translateX(100px);
    }
}

@keyframes mask-move {
    0% {
        transform: translateX(0);
    }

    100% {
        transform: translateX(100px);
    }
}

@-webkit-keyframes mask-inner-move {
    0% {
        transform: translateX(0);
    }

    100% {
        transform: translateX(-100px);
    }
}

@keyframes mask-inner-move {
    0% {
        transform: translateX(0);
    }

    100% {
        transform: translateX(-100px);
    }
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

#focus-text {
    background: #272643;
    padding-bottom: 50px;
}
#focus-text h2 {
    color: #FFFFFF;
}
.focus {
    margin-top: 40px;
    font-size: 3rem;
    text-transform: uppercase;
    color: white;
    letter-spacing: 0.2rem;
    line-height: 1;
    position: relative;
    width: 150px;
}

.focus:before {
    content: "Beom Seok";
    -webkit-filter: blur(3px);
            filter: blur(3px);
}

.focus:after {
    content: "";
    position: absolute;
    width: 50px;
    height: calc(100% + 8px);
    top: -4px;
    left: 0;
    border-top: 2px solid;
    border-bottom: 2px solid;
    -webkit-animation: mask-move 1s linear infinite alternate;
            animation: mask-move 1s linear infinite alternate;
}

.focus--mask {
    overflow: hidden;
    position: absolute;
    width: 50px;
    height: 100%;
    top: 0;
    left: 0;
    background: #444444;
    -webkit-animation: mask-move 1s linear infinite alternate;
            animation: mask-move 1s linear infinite alternate;
}

.focus--mask-inner {
    -webkit-animation: mask-inner-move 1s linear infinite alternate;
            animation: mask-inner-move 1s linear infinite alternate;
}

.credits {
    color: white;
    position: absolute;
    bottom: 0;
    left: 50%;
