#letters-in-and-out {
    height: 150px;
    position: relative;
    background: #272643;
    color: #FFFFFF;
}

#letters-in-and-out .word {
    text-align: center;
    position: absolute;
    opacity: 0;
    font-size: 50px;
    font-weight: 700;
}

#letters-in-and-out .letter {
    display: inline-block;
    position: relative;
    float: left;
    letter-spacing: 0.05rem;
    transform: translateZ(25px);
    transform-origin: 50% 50% 25px;
}

#letters-in-and-out .letter.out {
    transform: rotateX(90deg);
    transition: transform 0.32s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

#letters-in-and-out .letter.behind {
    transform: rotateX(-90deg);
}

#letters-in-and-out .letter.in {
    transform: rotateX(0deg);
    transition: transform 0.38s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
